import React, { useState } from 'react';
import { Container, Typography, Box, Button, Radio, RadioGroup, FormControlLabel, FormControl, FormLabel, Alert } from '@mui/material';
import {CopyBlock, dracula} from "react-code-blocks";

export interface Question {
    question: string;
    options: string[];
    answer: string;
    explanation: { [option: string]: string };
    code?: string; // Optional code attribute
}

interface QuestionnaireProps {
    questions: Question[];
    onSuccess: () => void;
}

const Questionnaire: React.FC<QuestionnaireProps> = ({ questions, onSuccess }) => {
    const [selectedAnswers, setSelectedAnswers] = useState<{ [key: number]: string }>({});
    const [showResults, setShowResults] = useState(false);
    const [isSuccessful, setIsSuccessful] = useState(false);

    const handleChange = (questionIndex: number, option: string) => {
        setSelectedAnswers({ ...selectedAnswers, [questionIndex]: option });
    };

    const handleSubmit = () => {
        setShowResults(true);
        const allCorrect = questions.every((q, index) => selectedAnswers[index] === q.answer);
        setIsSuccessful(allCorrect);
    };

    return (
        <Container maxWidth="md">
            <Box my={4}>
                <Typography variant="h4" component="h1" gutterBottom>
                    Lecture Questionnaire
                </Typography>
                {questions.map((q, index) => (
                    <Box key={index} my={2}>
                        <FormControl component="fieldset">
                            <FormLabel component="legend">{q.question}</FormLabel>
                            {q.code && (
                                <CopyBlock
                                    text={q.code}
                                    language="python"
                                    theme={dracula}
                                    codeBlock
                                />
                            )}
                            <RadioGroup
                                name={`question-${index}`}
                                value={selectedAnswers[index] || ''}
                                onChange={(e) => handleChange(index, e.target.value)}
                            >
                                {q.options.map((option, i) => (
                                    <FormControlLabel key={i} value={option} control={<Radio />} label={option} />
                                ))}
                            </RadioGroup>
                        </FormControl>
                        {showResults && (
                            <>
                                {selectedAnswers[index] === q.answer ? (
                                    <Alert severity="success">Correct</Alert>
                                ) : (
                                    <Alert severity="error">{q.explanation[selectedAnswers[index]]}</Alert>
                                )}
                            </>
                        )}
                    </Box>
                ))}
                {!isSuccessful &&<Button variant="contained" color="primary" onClick={handleSubmit}>
                    Submit
                </Button>}
                {showResults && isSuccessful && (
                    <Button variant="contained" color="secondary" onClick={onSuccess} style={{ marginTop: '16px' }}>
                        Continue
                    </Button>
                )}
            </Box>
        </Container>
    );
};

export default Questionnaire;
