import React from 'react';
import { useNavigate } from 'react-router-dom';
import Questionnaire, {Question} from '../../Questionnaire';

const questions: Question[] = [
    {
        question: 'Why is it important to use descriptive names for variables?',
        options: [
            'To make the code more readable and maintainable',
            'To make the code harder to understand',
            'To increase the execution speed of the code',
            'To make the code look longer'
        ],
        answer: 'To make the code more readable and maintainable',
        explanation: {
            'To make the code more readable and maintainable': 'Correct. Descriptive names help make the code easier to read and maintain.',
            'To make the code harder to understand': 'Incorrect. Descriptive names do the opposite; they make the code easier to understand.',
            'To increase the execution speed of the code': 'Incorrect. Descriptive names do not affect the execution speed of the code.',
            'To make the code look longer': 'Incorrect. The goal is readability, not length.'
        }
    },
    {
        question: 'What is the preferred way to separate words in variable names in Python?',
        options: [
            'CamelCase',
            'snake_case',
            'PascalCase',
            'kebab-case'
        ],
        answer: 'snake_case',
        explanation: {
            'CamelCase': 'Incorrect. CamelCase is commonly used in other languages, but snake_case is preferred in Python.',
            'snake_case': 'Correct. snake_case is the preferred convention for variable names in Python.',
            'PascalCase': 'Incorrect. PascalCase is similar to CamelCase but typically used for class names.',
            'kebab-case': 'Incorrect. kebab-case is not used in Python variable naming.'
        }
    },
    {
        question: 'When is it acceptable to use single character names for variables?',
        options: [
            'Always',
            'Never',
            'Only for loop variables',
            'Only for global variables'
        ],
        answer: 'Only for loop variables',
        explanation: {
            'Always': 'Incorrect. Single character names should generally be avoided except in specific cases.',
            'Never': 'Incorrect. Single character names can be used for loop variables.',
            'Only for loop variables': 'Correct. Single character names are acceptable for loop variables where the context is clear.',
            'Only for global variables': 'Incorrect. Global variables should have descriptive names.'
        }
    },
    {
        question: 'Which of the following is a good variable name for storing the speed of a planet in the x direction?',
        options: [
            'sx',
            'speed_x',
            'spdx',
            'spd_x'
        ],
        answer: 'speed_x',
        explanation: {
            'sx': 'Incorrect. This is too short and not descriptive enough.',
            'speed_x': 'Correct. This name is descriptive and follows the snake_case convention.',
            'spdx': 'Incorrect. This is an abbreviation that is not very clear.',
            'spd_x': 'Incorrect. While closer, speed_x is more descriptive.'
        }
    },
];

const Lecture4Questionnaire: React.FC = () => {
    const navigate = useNavigate();

    const handleSuccess = () => {
        const completionStatus = JSON.parse(localStorage.getItem('completionStatus') || '{}');
        completionStatus['lecture4Questionnaire'] = true;
        localStorage.setItem('completionStatus', JSON.stringify(completionStatus));
        navigate('/week1');
    };

    return <Questionnaire questions={questions} onSuccess={handleSuccess} />;
};

export default Lecture4Questionnaire;
